/**
 * Name: Bizagi Workportal Users Modal
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.desktop.widgets.admin.userPermissionReport", {}, {
  /**
                                                                                                               * Constructor
                                                                                                               */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);
    //Load template
    self.loadTemplates({
      "user-permission-report": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.userPermissionReport").concat("#user-permission-report") });

  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate('user-permission-report');
    self.content = template.render({});
    return self.content;
  },

  postRender: function postRender() {
    var self = this;
    $("#downloadButtonActive", self.getContent()).click(function () {
      self.dataService.getUserPermissionsExcel(true);
    });
    $("#downloadButtonInactive", self.getContent()).click(function () {
      self.dataService.getUserPermissionsExcel(false);
    });
  } });



bizagi.injector.register('bizagi.workportal.desktop.widgets.admin.userPermissionReport', ['workportalFacade', 'dataService', bizagi.workportal.desktop.widgets.admin.userPermissionReport]);