bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.admin.business.polices", {}, {
  /*
                                                                                                   *   Constructor
                                                                                                   */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load templates
    self.loadTemplates({
      "widgets-admin-business-polices": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.business.polices").concat("#business-polices-wrapper") });

  },

  /**
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    var generalTmpl = self.getTemplate("widgets-admin-business-polices");

    self.content = generalTmpl.render({ 'build': bizagi.loader.build });
    console.log('renderizar', self.content);
    return self.content;
  },

  clean: function clean() {
    var self = this;
  } });


bizagi.injector.register("bizagi.workportal.widgets.admin.business.polices", ["workportalFacade", "dataService", bizagi.workportal.widgets.admin.business.polices]);