/**
 * Name: BizAgi Desktop Widget Administration preferences Implementation
 * 
 * @author Andres Fernando Muñoz
 */

bizagi.workportal.widgets.admin.preferences.extend("bizagi.workportal.widgets.admin.preferences", {}, {

  init: function init(workportalFacade, dataService, params) {
    var self = this;
    self.bizagiUserPreferences = new bizagi.services.preferences();
    self.bizagiCustomFormats = new bizagi.services.preferences.customFormats();

    self.guidForm;
    self.isNewForm;

    self.notifier = bizagi.injector.get("notifier");

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load templates
    self.loadTemplates({
      "admin.preferences.wrapper": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.preferences").concat("#ui-bizagi-workportal-widget-admin-preferences-wrapper"),
      "admin.preferences.container": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.preferences").concat("#ui-bizagi-workportal-widget-preferences-container"),
      "admin.preferences.buttons": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.preferences").concat("#ui-bizagi-workportal-widget-preference-buttons"),
      "admin.preferences.iframe": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.preferences").concat("#ui-bizagi-workportal-widget-preference-iframe"),
      "admin.preferences.error": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.preferences").concat("#ui-bizagi-workportal-widget-preferences-errormessage"),
      "admin.preferences.properties": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.preferences").concat("#ui-bizagi-workportal-widget-preferences-properties"),
      useNewEngine: false });


  },

  loadtemplates: function loadtemplates() {
    var self = this;

    self.container = self.getTemplate("admin.preferences.container");
    self.buttons = self.getTemplate("admin.preferences.buttons");
    self.iframe = self.getTemplate("admin.preferences.iframe");
    self.errorMessage = self.getTemplate("admin.preferences.error");
    self.propertiesTmpl = self.getTemplate("admin.preferences.properties");
  },

  postRender: function postRender() {
    var self = this;

    $.when(self.dataService.getPreferenceFormParams()).done(function (data) {

      self.guidForm = data.id !== "-1" ? data.id : "";

      if (data.errorMessage === true) {
        self.renderErrorMessage();
      } else if (data.isNewForm === true) {
        self.renderPreferences();
      } else {
        self.renderPreferencesOldForm();
      }
    });

  },

  renderErrorMessage: function renderErrorMessage() {
    var self = this;

    var resource = self.getResource("workportal-widget-admin-entities-message-migrate");
    resource = resource.replace("{build}", bizagi.loader.productBuildToAbout);

    var errorContent = $.tmpl(self.errorMessage, { errorMessage: resource });

    self.content.html(errorContent);

  },

  setupData: function setupData() {
    var self = this;
    var content = self.getContent();
  },

  renderPreferencesOldForm: function renderPreferencesOldForm() {

    var self = this;

    var $content = self.getContent();
    var url = self.dataService.getUrl({
      "endPoint": "PreferenceFormOld" }) +
    "?idForm=" + self.guidForm + "&referer=userPreferences";

    var $iframeContent = $.tmpl(self.iframe, { "url": url });
    var $iframe = $("iframe", $iframeContent);

    $iframeContent.addClass('loading-iframe');

    $iframe.hide();

    $iframe.load(function () {
      $(this).fadeIn();
    });

    $content.html($iframeContent);

  },

  renderPreferences: function renderPreferences() {
    var self = this;
    var readyDeferred = new $.Deferred();
    var content = self.getContent();
    self.currentUser = bizagi.currentUser.idUser;

    var preferencesParams = {
      h_action: "LOADUSERPREFERENCESFORM",
      h_contexttype: "entity",
      h_guidForm: self.guidForm };


    var renderContainer = $.tmpl(self.container);

    bizagi.loader.start("rendering").then(function () {
      var rendering = new bizagi.rendering.facade();
      //request to preferences definition service
      self.dataService.getPreferencesForm(preferencesParams).done(function (data) {
        var renderingParams = {
          canvas: renderContainer,
          data: data,
          context: "userPreferencesForm" };

        rendering.execute(renderingParams).done(function (form) {
          var formValues = {};
          $.extend(formValues, self.bizagiUserPreferences.getFormPreferencesValues(), self.bizagiCustomFormats.getFormatValues());
          $("#preferences-tabs").tabs();
          $("#properties", content).html($.tmpl(
          self.propertiesTmpl,
          formValues,
          {
            formatTime: self.bizagiCustomFormats.formatTime }));


          var noPermission = true;
          if (bizagi.menuSecurity.OpenWindowsMaximized == true) {
            $("#displayoptions_OpenWindowsMaximized", content).show();
            noPermission = false;
          }
          if (bizagi.menuSecurity.AlwaysCollapseActivitySidebars == true) {
            $("#displayoptions_collapseSidebars", content).show();
            noPermission = false;
          }
          if (bizagi.menuSecurity.ReturnToInboxWhenTasksAreFinished == true) {
            $("#displayoptions_goToInbox", content).show();
            noPermission = false;
          }
          if (noPermission) {
            $("#noPermission", content).show();
          }

          $("#preferences-tabs-ul").show();
          self.preferenceForm = rendering.form;
          self.preferenceForm && self.preferenceForm.properties ? self.preferenceForm.properties.contexttype ? "" : self.preferenceForm.properties.contexttype = "entity" : "";
          $("#preferences", content).html(renderContainer);
          self.handleEvents();
          readyDeferred.resolve();
        });
      });
    });

    return readyDeferred;
  },

  /*
     *  Buttons for preferences actions
     */
  handleEvents: function handleEvents() {
    var self = this;
    var content = self.getContent();
    $("#preferences-buttons", content).html($.tmpl(self.buttons));

    $("#update-preferences", content).click(function (e) {
      self.updateData();
    });

    var dialog = content && content.length ? content.closest(".ui-dialog") : [];
    if (dialog.length > 0 && self.preferenceForm) {
      dialog.on("dialogmaximize", function (evt) {
        self.preferenceForm.triggerHandler("maximazedForm");
        dialog.trigger("resize");
      });

      dialog.on("dialogunmaximize", function (evt) {
        self.preferenceForm.triggerHandler("unmaximazedForm");
        dialog.trigger("resize");
      });
    }
  },

  updatePreferences: function updatePreferences() {
    var self = this;
    var error = self.getResource("workportal-widget-admin-preferences-error-same-value");
    var values = self.getUserPreferences();

    if (values.decimalSeparator && values.decimalSeparator === values.thousandSeparator) {
      bizagi.showMessageBox(error, "Error");
    } else {
      self.bizagiUserPreferences.overridePreferences(values);
      self.notifier.showSucessMessage(bizagi.localization.getResource("workportal-widget-admin-displayoptions-success"), bizagi.localization.getResource("workportal-menu-preferences"), "");
    }

  },

  getUserPreferences: function getUserPreferences() {
    var self = this;
    var values = self.bizagiUserPreferences.getPreferences();
    var $inputs = $('.preferences-properties :checkbox');
    $inputs.each(function () {
      values[this.name] = this.checked;
    });
    return values;
  },
  updateData: function updateData() {
    var self = this;
    if (self.preferenceForm.validateForm()) {
      var serviceRender = new bizagi.render.services.service();
      var data = self.preferenceForm.collectRenderValuesForSubmit();
      var content = self.getContent();

      self._startLoading(content, true);

      $.when(serviceRender.multiactionService.submitData({
        action: "SAVEUSERPREFERENCES",
        contexttype: "entity",
        data: data,
        surrogatekey: self.currentUser,
        idPageCache: self.preferenceForm.idPageCache })).
      done($.proxy(function (message) {
        //Change language on sessionStorage if change language on preferences
        if (data.language) {
          $.when(self.dataService.getCurrentUser()).done(function (currentUserData) {
            // Changing languaje
            $.when(assignLanguage(currentUserData.language)).done(function () {
              bizagi.util.changeData({ language: currentUserData.language });
              // Rendering
              self.executeRender();
            }).fail(function () {});
          }).fail(function () {});
        } else {
          // Rendering
          self.executeRender();
        }
        self.updatePreferences();

      }, data)).fail(function (data) {
        var typeMessage = bizagi.localization.getResource("workportal-general-word-message");
        if (data.message !== undefined)
        bizagi.showMessageBox(data.message, typeMessage);
        self.executeRender();
      });
    } else {
      $("#preferences-tabs").tabs({ active: 0 });
    }
  },
  executeRender: function executeRender() {
    var self = this;
    var content = self.getContent();
    $.when(self.renderPreferences()).
    always(self._startLoading.bind(self, content, false));
  },

  /**
      * hide elements while loader and add loading class to content
      * @param {JQuery Element} content 
      */
  _startLoading: function _startLoading(content, showLoader) {
    var _newDisplay = showLoader === true ? "none" : "block";

    content.find("#preferences-tabs").css("display", _newDisplay);
    content.find("#preferences-buttons").css("display", _newDisplay);

    if (showLoader) {
      content.addClass("loading-iframe");
    } else {
      content.removeClass("loading-iframe");
    }
  } });