bizagi.workportal.widgets.admin.usersDelegation.searchUser.extend("bizagi.workportal.widgets.admin.usersDelegation.searchUser", {}, {

  init: function init(workportalFacade, dataService, params) {
    var self = this;
    self._super(workportalFacade, dataService, params);

    self.filterData = {};
    self.maxRows = 8;
    self.maxPages = 10;
    self.$canvas = params.canvas;
    self.users = [];
    self.totalPages = 0;
    self.currentPage = 1;
    self.orderType = "ASC";
    self.orderField = "";
    self.tableHeaders = [
    { "displayName": self.getResource("workportal-widget-usertable-id"), "fieldValue": "idUser" },
    { "displayName": self.getResource("workportal-widget-usertable-user"), "fieldValue": "fullName" },
    { "displayName": self.getResource("workportal-widget-usertable-name"), "fieldValue": "userName" },
    { "displayName": self.getResource("workportal-widget-usertable-domain"), "fieldValue": "domain" },
    { "displayName": self.getResource("workportal-widget-usertable-email"), "fieldValue": "contactEmail" },
    { "displayName": self.getResource("workportal-widget-usertable-active-assign"), "fieldValue": "enabledForAssignation" },
    { "displayName": self.getResource("workportal-widget-usertable-active"), "fieldValue": "enabled" }];


    self.loadTemplates({
      "searchUser": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation.searchUser").concat("#ui-bizagi-workportal-widget-search-user-tmpl"),
      "searchUser.form": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation.searchUser").concat("#ui-bizagi-workportal-widget-search-user-form-tmpl"),
      "searchUser.message": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation.searchUser").concat("#ui-bizagi-workportal-widget-search-user-message-tmpl"),
      "searchUser.table.wrapper.final": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation.searchUser").concat("#ui-bizagi-workportal-widget-search-user-table-wrapper-final-tmpl"),
      useNewEngine: false });


    bizagi.templateService.getTemplateWidget(
    bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation.searchUser").concat("#ui-bizagi-workportal-widget-search-user-table-final-tmpl")).
    done(function (tmpl) {
      self.tmpl["searchUser.table.final"] = tmpl.markup;
    });
  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("searchUser");
    var content;

    content = self.content = $.tmpl(template, {});

    // Override canvas if it has been defined
    if (self.params.canvas) {
      content = $(self.params.canvas).append(content);
    }

    return content;
  },

  postRender: function postRender() {
    var self = this;

    //load templates
    self.loadtemplates(); //  >> 1

    //load form data
    self.setupData(); // >> 2
  },

  /* >> 1
      * Load template vars
      */
  loadtemplates: function loadtemplates() {
    var self = this;

    self.usersTableFormTmpl = self.getTemplate("searchUser.form");
    self.usersTableMessage = self.getTemplate("searchUser.message");

    self.usersTableTmpl = self.getTemplate("searchUser.table.wrapper.final");
    self.usersTableTmplFinal = self.getTemplate("searchUser.table.final");
  },

  /* >> 2
      * Setup form data
      */
  setupData: function setupData() {
    var self = this;
    var resultTemplate = $.tmpl(self.usersTableFormTmpl, {});

    var currentRoles = [];
    var currentSkills = [];
    var currentPositions = [];
    var currentOrganizations = [];

    $.when(self.dataService.getPreferencesCurrentUser()).done(function (data) {
      currentRoles = data.sRoles || [];
      currentSkills = data.sSkills || [];
      currentPositions = data.sPositions || [];
      currentOrganizations = data.sOrganizations || [];
    }).fail(function (error) {
      bizagi.log(error);
    });

    var locationDef = $.when(self.getValuesMultiple("LOCATION"));

    var roleDef = $.when(self.getValuesMultiple("ROLE"));

    var skillDef = $.when(self.getValuesMultiple("SKILL"));

    var organizationDef = $.when(self.getValuesMultiple("ORGPOSITION"));

    var positionDef = $.when(self.getValuesMultiple("ORG"));



    var deferreds = [locationDef, roleDef, skillDef, organizationDef, positionDef];

    $.when.apply($, deferreds).then(function (allLocations, allRoles, allSkills, allPositions, allOrganizations) {
      $("#ui-bizagi-workportal-widget-search-user__form-wrapper", self.content).html("");
      $("#ui-bizagi-workportal-widget-search-user__form-wrapper", self.content).html(resultTemplate);

      self.locationsComponent = self.configureProcessCombo(allLocations);

      var rolesWrapper = $("#widget-search-user__roles");
      self.rolesComponent = self.setupMultiselector(rolesWrapper, allRoles, currentRoles);

      var skillsWrapper = $("#widget-search-user__skills");
      self.skillsComponent = self.setupMultiselector(skillsWrapper, allSkills, currentSkills);

      var positionsWrapper = $("#widget-search-user__positions");
      self.positionsComponent = self.setupMultiselector(positionsWrapper, allPositions, currentPositions);

      var organizationsWrapper = $("#widget-search-user__organizations");
      self.organizationsComponent = self.setupMultiselector(organizationsWrapper, allOrganizations, currentOrganizations);

      self.setupEventsHandler();
      self.$canvas.trigger("completeUserForm");

      $("#ui-bizagi-workportal-widget-search-user__backfilters-btn", self.content).click(function () {
        $(".widget-userdelegation__bread-crumbs__link-1").click();
      });
    });

  },

  // >> 2.1
  getValuesMultiple: function getValuesMultiple(entity) {
    var self = this;
    var defer = $.Deferred();
    var params = '{"select":[{"type":"Entity","children": [{"type":"EntityValue","data":"*"}],"data":"*","where":{"name":"' + entity + '"}}]}';
    $.when(self.dataService.getValuesFromCQL(params)).done(function (data) {
      var options = [];
      if (data && data[0]) {
        var guidRef = JSON.parse(data[0].content).displayAttribute.baref.ref;
        $.each(data[0].children, function () {
          var idEntity = JSON.parse(this.content).idEntityValue;
          var fields = self.allKeysToUpperCase(JSON.parse(this.content).fields);
          var displayName = fields[guidRef.toUpperCase()];
          options.push({ "id": idEntity, "displayName": displayName });
        });
        options.sort(function (a, b) {return a.displayName > b.displayName ? 1 : b.displayName > a.displayName ? -1 : 0;});
      }
      defer.resolve(options);
    }).fail(function (error) {
      bizagi.log(error);
    });
    return defer.promise();
  },

  // >> 2.1.1
  allKeysToUpperCase: function allKeysToUpperCase(obj) {
    var self = this;
    var output = {};
    for (var i in obj) {
      if (Object.prototype.toString.apply(obj[i]) === "[object Object]") {
        output[i.toUpperCase()] = self.allKeysToUpperCase(obj[i]);
      } else {
        output[i.toUpperCase()] = obj[i];
      }
    }
    return output;
  },

  // >> 2.2
  configureProcessCombo: function configureProcessCombo(options) {
    var self = this;
    var processComboWrapper = $("#widget-search-user__locations", self.content);
    var comboDataSource = { combo: [], id: "widget-search-user__locations-to-delegate-combo" };
    comboDataSource.combo = comboDataSource.combo.concat(options);
    processComboWrapper.empty();
    processComboWrapper.uicombo({
      isEditable: false,
      data: comboDataSource,
      itemValue: function itemValue(obj) {
        return obj.id;
      },
      itemText: function itemText(obj) {
        return obj.displayName;
      },
      onComplete: function onComplete() {
      },
      onChange: function onChange(obj) {
        var value = obj.ui.data("value");
        self.idProcess = value;
        $(".biz-wp-combo-input", processComboWrapper).css({ "box-shadow": "none" });
      }
      //initValue: comboDataSource.combo[0]
    });
    return processComboWrapper;
  },

  /* >> 2.3
      * Multiselector
      */

  setupMultiselector: function setupMultiselector(element, options, current) {
    var self = this;
    var component = {
      element: element,
      options: options,
      values: [] };


    //options.unshift({displayName: " ", id: ""});
    element.uicombo({
      data: { combo: options },
      // initValue: options[0],
      itemValue: function itemValue(item) {
        return item.id;
      },
      itemText: function itemText(item) {
        return item.displayName;
      },
      onComplete: function onComplete() {
      },
      onChange: function onChange(obj) {
        var value = obj.ui.data("value");
        component = self.addChoiceMultiselector(component, value);
      },
      onComboDropDown: function onComboDropDown() {
        self.markChoicesMultiselector(component);
      } });

    $(".biz-wp-combo-input", element).addClass("widget-search-user__multiselector-input");
    $(element).prepend('<div class="widget-search-user__multiselector"></div>');

    $.each(current, function () {
      component = self.addChoiceMultiselector(component, this.id);
    });

    return component;
  },

  // >> 2.3.1
  addChoiceMultiselector: function addChoiceMultiselector(component, value) {
    var selectedOption = component.options.filter(function (item) {
      return item.id == value;
    })[0];
    if (component.values.indexOf(value + "") === -1 && selectedOption != null) {
      var displayName = selectedOption.displayName;
      $(".widget-search-user__multiselector", component.element).append('<div class="widget-search-user__multiselector__item" data-value =' + value + ">" + displayName + ' | <i class="widget-search-user__multiselector__item-clear-btn bz-icon bz-icon-close"></i></div>');
      $(".biz-wp-combo-input", component.element).css({ "box-shadow": "none", "color": "white" });
      component.values.push(value + "");
      $(".widget-search-user__multiselector__item-clear-btn", component.element).click(function () {
        var itemToDelete = $(this).parent();
        var indexToRemove = component.values.indexOf(itemToDelete.data().value + "");
        if (indexToRemove >= 0) {
          component.values.splice(indexToRemove, 1);
          itemToDelete.remove();
          if (component.values.length <= 0) {
            $(".biz-wp-combo", component.element).show();
          }
        }
      });
    }
    return component;
  },

  // >> 2.3.2
  markChoicesMultiselector: function markChoicesMultiselector(component) {
    $("li", component.element).removeClass("widget-search-user__multiselector__option--selected");
    $("li", component.element).prepend('<i class="widget-search-user__multiselector__option__icon"></i>');
    $.each(component.values, function (index, value) {
      $('li[data-value="' + value + '"]', component.element).addClass("widget-search-user__multiselector__option--selected");
    });
  },

  /* >> 2.4
      * Implement main buttons behaviour
      */
  setupEventsHandler: function setupEventsHandler() {
    var self = this;
    var content = self.getContent();
    var $userForm = $("#ui-bizagi-workportal-widget-search-user__form-wrapper form", content);

    $userForm.on("submit", function (e) {

      e.preventDefault();

      self.currentPage = 1;
      self.orderType = "ASC";
      self.orderField = "";
      var localizationArray = [];
      var localizationSelecction = $(this.elements["widget-search-user__locations-to-delegate-combo"]).data("value");
      if (localizationSelecction != null && localizationSelecction != "") localizationArray.push(localizationSelecction);

      self.filterData = {
        domain: this.elements.domainInput.value,
        userName: this.elements.userFullName.value,
        fullName: this.elements.userNameReassignInput.value,
        locations: JSON.stringify(localizationArray),
        pag: self.currentPage,
        pagSize: self.maxRows,
        orderField: self.orderField,
        orderType: self.orderType };



      self.getUsersTableData(
      {
        idUser: bizagi.currentUser.idUser,
        organization: JSON.stringify(self.organizationsComponent.values),
        roles: JSON.stringify(self.rolesComponent.values),
        skills: JSON.stringify(self.skillsComponent.values),
        locations: JSON.stringify(localizationArray),
        positions: JSON.stringify(self.positionsComponent.values) });



    });
    $userForm.on('keyup keypress', function (e) {
      var keyCode = e.keyCode || e.which;
      if (keyCode === 13) {
        e.preventDefault();
        $userForm.submit();
        return false;
      }
    });

    $userForm.on("reset", function (event) {
      event.preventDefault();
      $(this.elements["widget-search-user__locations-to-delegate-combo"]).data("value", "").val("");
      this.elements.domainInput.value = "";
      this.elements.userFullName.value = "";
      this.elements.userNameReassignInput.value = "";

      self.cleanForm();
      $("#ui-bizagi-workportal-widget-search-user__list-wrapper", content).empty();
    });

  },

  /* >> 2.4.2
      * Clean Form
      */
  cleanForm: function cleanForm() {
    var self = this;
    $("#widget-search-user__locations", self.content).val("");
    self.clearMultiselector(self.organizationsComponent);
    self.clearMultiselector(self.rolesComponent);
    self.clearMultiselector(self.skillsComponent);
    self.clearMultiselector(self.positionsComponent);
  },

  // >> 2.4.2.1
  clearMultiselector: function clearMultiselector(component) {
    component.element.val("");
    $(".widget-search-user__multiselector", component.element).html("");
    component.values = [];
  },


  /* >> 2.4.1
      * get users table data
      */
  getUsersTableData: function getUsersTableData(params) {
    var self = this;

    params = params || {};
    self.orderType = params.orderType || self.orderType;
    self.orderField = params.orderField || self.orderField;

    $.when(self.dataService.getUsersForDelegation($.extend(self.filterData, params))).done(function (data) {
      self.showUsersTable(data);
    }).fail(function (error) {
      bizagi.log(error);
    });

  },

  /* >> 2.4.1.1
      * if users returned show the table
      */
  showUsersTable: function showUsersTable(data) {

    var self = this;
    self.users = data.users;

    var $wrapperList = $("#ui-bizagi-workportal-widget-search-user__list-wrapper", self.content);
    var $wrapperForm = $("#ui-bizagi-workportal-widget-search-user__form-wrapper", self.content);
    var $breadCrumbResults = $(".widget-userdelegation__bread-crumbs li:last-child");

    if (self.users.length > 0) {
      var msg = bizagi.localization.getResource("workportal-widget-userdelegation-bread-crumb-3");
      $(".ui-widget-header.ui-corner-all > span.ui-dialog-title").html(msg);

      $wrapperList.empty();
      $wrapperList.show();

      var usersTableTmpl = $.tmpl(self.usersTableTmpl, {});
      $wrapperList.html(usersTableTmpl);
      self.tableFinal = new Vue({
        el: $('#table', $wrapperList)[0],
        template: self.usersTableTmplFinal,
        data: {
          users: self.users,
          headers: self.tableHeaders,
          orderField: self.orderField,
          orderType: self.orderType,
          renderBooleanYes: self.getResource('render-boolean-yes'),
          renderBooleanNo: self.getResource('render-boolean-no'),
          selectedUser: '' },

        methods: {
          selectUser: function selectUser(userId) {
            this.selectedUser = userId;
          } } });



      $wrapperForm.hide();
      $breadCrumbResults.show();
      $("#ui-bizagi-workportal-widget-search-user__selected-btn", self.content).click(function () {
        if (self.tableFinal.selectedUser !== '') {
          var msg = bizagi.localization.getResource("workportal-widget-userdelegation-bread-crumb-1");
          $(".ui-widget-header.ui-corner-all > span.ui-dialog-title").html(msg);

          self.params.parentDef.resolve(self.tableFinal.selectedUser.idUser, self.tableFinal.selectedUser.userName);

          if (self.params.widgetName == "usersDelegation" && bizagi.workportal.desktop.dialogStack.length > 0) {
            bizagi.workportal.desktop.dialogStack[bizagi.workportal.desktop.dialogStack.length - 1].dialogBox.on("dialogbeforeclose", function () {
              // Next variable specifies if is redirecting to inbox when the result status does not have errors.
              if (self.resultStatus === true)
              self.publish("changeWidget", {
                widgetName: bizagi.workportal.currentInboxView });

            });
          }

          // Unbind an destroy
          self.tableFinal.$destroy();
          $(this).unbind("click");
          $("#ui-bizagi-workportal-widget-search-user__back-btn", self.content).unbind("click");
        } else {
          // THIS CODE DO NOTHING
          // publish excecutes twice when create a new instance of userstable
          self.$canvas.trigger("clickUserLink", { id: this.id, name: this.name, type: $(this).data("linkname") });
        }
      });
      $("#ui-bizagi-workportal-widget-search-user__back-btn", self.content).click(function () {
        var $wrapperList = $("#ui-bizagi-workportal-widget-search-user__list-wrapper", self.content);
        var $wrapperForm = $("#ui-bizagi-workportal-widget-search-user__form-wrapper", self.content);
        var $breadCrumbResults = $(".widget-userdelegation__bread-crumbs li:last-child");
        var msg = bizagi.localization.getResource("workportal-widget-userdelegation-bread-crumb-2");
        $(".ui-widget-header.ui-corner-all > span.ui-dialog-title").html(msg);
        $wrapperList.empty();
        $wrapperForm.show();
        $wrapperList.hide();
        $breadCrumbResults.hide();

        // Unbind an destroy
        self.tableFinal.$destroy();
        $(this).unbind("click");
        $("#ui-bizagi-workportal-widget-search-user__selected-btn", self.content).unbind("click");
      });
    } else {
      var msg = bizagi.localization.getResource("workportal-widget-usertable-empty");
      $(".bz-wp-table-centermessage", self.content).find('h3').text(msg);
      setTimeout(function () {
        $(".bz-wp-table-centermessage", self.content).find('h3').text('');
      }, 3000);
    }
  } });