/*
*   Name: BizAgi Workportal Users Delegation
*   Author: Ivan Ricardo Taimal
*   Comments:
*   -   This script renders the user delegation
*/

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.admin.usersDelegation", {}, {

  init: function init(workportalFacade, dataService, params) {

    var self = this;

    self._super(workportalFacade, dataService, params);

    //Load templates
    self.loadTemplates({
      "admin.userdelegation": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation").concat("#ui-bizagi-workportal-widget-admin-userdelegation"),
      "admin.userdelegationListTmpl": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation").concat("#bz-wp-widget-userdelegation-configurations-tmpl"),
      "admin.userdelegationDeleteConfirmationTmpl": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation").concat("#bz-wp-widget-userdelegation-delete-confirmation-tmpl"),
      "admin.userdelegationFormTmpl": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation").concat("#bz-wp-widget-userdelegation-form-tmpl"),
      "admin.userdelegationEmptyMessageTmpl": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.usersDelegation").concat("#bz-wp-widget-userdelegation-configurations-message-tmpl"),
      useNewEngine: false });

  },

  /*
     *   Returns the widget name
     */
  getWidgetName: function getWidgetName() {
    return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_ADMIN_USERS_DELEGATION;
  },

  /*
     *   Renders the content for the current controller
     *   Returns a deferred because it has to load the current user
     */
  renderContent: function renderContent() {
    var self = this;
    var tmpl = self.getTemplate("admin.userdelegation");
    var content = self.content = $.tmpl(tmpl);
    return content;
  } });