/**
 * Admin module to export generated error logs.
 * @author Francisco Murcia
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.admin.error.logs", {}, {

  getWidgetName: function getWidgetName() {
    return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_ADMIN_ERROR_LOGS;
  },

  init: function init(workportalFacade, dataService, params) {
    var self = this;
    var templateName = "bizagi.workportal.desktop.widgets.admin.error.logs";

    // Call base
    self._super(workportalFacade, dataService, params);

    // Load templates
    self.loadTemplates({
      "admin.errorlogs": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-errorlogs"),
      "admin.errorlogs.detail": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-errorlogs-detail"),
      "admin.errorlogs.export": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-errorlogs-export"),
      useNewEngine: false });


    self.dateFormat = bizagi.localization.getResource("dateFormat");
    self.timeFormat = bizagi.localization.getResource("timeFormat");

    // Model's declaration
    self.CurrentClientData = {
      selectedGuids: [] };

  },

  renderContent: function renderContent() {
    var self = this;

    self.content = $.tmpl(self.getTemplate("admin.errorlogs"));

    return self.content;
  },

  postRender: function postRender() {
    var self = this;

    self.setupSearchFilter();
    self.setupDatePicker();
    self.setupFilterButtons();
    self.setupExportButton();
    self.setupJSGrid();
  },

  setupSearchFilter: function setupSearchFilter() {
    var self = this;

    $("#admin-errorlogs-filter-search", self.getContent()).on('input', function () {
      if ($(this).val() != "") {
        self.changeSearchForm();
      }
    });

    $("#admin-errorlogs-filter-search", self.getContent()).on('keyup', function (e) {
      if (e.key === 'Enter' || e.keyCode === 13) {
        if ($(this).val() != "") {
          self.applyFilter();
        }
      }
    });
  },

  setupDatePicker: function setupDatePicker() {
    var self = this;
    var lang = "en";

    if (bizagi.util.languages[BIZAGI_LANGUAGE]) {
      lang = bizagi.util.languages[BIZAGI_LANGUAGE].substring(0, 2);
    }

    $("#admin-errorlogs-filter-startDate", self.getContent()).mobiscroll().calendar({
      controls: ['calendar', 'time'],
      cssClass: "mbsc-bizagi-theme",
      display: "bubble",
      focusTrap: false,
      lang: lang,
      onBeforeShow: function onBeforeShow(event, inst) {
        var value = inst.getVal();
        if (!value) {
          value = new Date();
          value.setHours(0);
          value.setMinutes(0);
          value.setSeconds(0);
          inst.setVal(value);
        }

        return true;
      },
      onSet: function onSet(event, inst) {
        self.changeSearchForm();
      } });


    $("#admin-errorlog-filter-endDate", self.getContent()).mobiscroll().calendar({
      controls: ['calendar', 'time'],
      cssClass: "mbsc-bizagi-theme",
      display: "bubble",
      focusTrap: false,
      lang: lang,
      onSet: function onSet(event, inst) {
        self.changeSearchForm();
      } });

  },

  changeSearchForm: function changeSearchForm() {
    var self = this;

    $("#btn-admin-errorlogs-filter-apply>span", self.getContent()).prop('textContent',
    bizagi.localization.getResource("workportal-widget-admin-errorlogs-filter-apply"));
    $("#btn-admin-errorlogs-filter-reset").css('visibility', 'visible');
  },

  clearSearchForm: function clearSearchForm() {
    var self = this;

    $("#btn-admin-errorlogs-filter-apply>span", self.getContent()).prop('textContent',
    bizagi.localization.getResource("workportal-widget-admin-errorlogs-filter-refresh"));
    $("#btn-admin-errorlogs-filter-reset").css('visibility', 'hidden');
  },

  setupFilterButtons: function setupFilterButtons() {
    var self = this;

    $("#btn-admin-errorlogs-filter-apply", self.getContent()).click(function () {
      self.applyFilter();
    });

    $("#btn-admin-errorlogs-filter-reset", self.getContent()).click(function () {
      self.resetFilter();
    });
  },

  applyFilter: function applyFilter() {
    var self = this;

    var filter = $("#admin-errorlogs-filter-search").prop('value');
    var startTime = $("#admin-errorlogs-filter-startDate").mobiscroll('getVal');
    var endTime = $("#admin-errorlog-filter-endDate").mobiscroll('getVal');

    if (filter || startTime && endTime && endTime.getTime() > startTime.getTime()) {
      $("#admin-errorlogs-grid").jsGrid('loadData', {
        startTime: startTime ? self.getTicks(startTime) : '',
        endTime: endTime ? self.getTicks(endTime) : '',
        filter: filter });

    } else {
      $("#admin-errorlogs-grid").jsGrid('loadData');
    }
  },

  resetFilter: function resetFilter() {
    var self = this;

    $("#admin-errorlogs-filter-search").val('');
    $(".biz-wp-form-datepicker").mobiscroll('clear');
    $("#btn-admin-errorlogs-filter-reset").css('visibility', 'hidden');
    $("#btn-admin-errorlogs-filter-apply>span", self.getContent()).prop('textContent',
    bizagi.localization.getResource("workportal-widget-admin-errorlogs-filter-refresh"));
    $("#admin-errorlogs-grid").jsGrid('loadData');
  },

  getTicks: function getTicks(datetime) {
    var offset = datetime.getTimezoneOffset() * 600000000;
    var ticks = datetime.getTime() * 10000;

    return ticks - offset;
  },

  setupExportButton: function setupExportButton() {
    var self = this;

    $("#btn-admin-errorlogs-export", self.getContent()).click(function () {
      if (self.CurrentClientData.selectedGuids.length <= 0) return;
      self.dataService.getErrorLogsArchive(self.CurrentClientData.selectedGuids).done(
      function (response) {
        if (response.ZipContent) {
          self.showExportDialog($("#admin-errorlogs-table"), response);
        }
      });

    });
  },

  setupJSGrid: function setupJSGrid() {
    var self = this;

    var titleId = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-id');
    var titleDateTime = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-datetime');
    var titleUser = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-username');
    var titleCase = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-casenumber');
    var titleNode = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-sourcenode');
    var titleDescription = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-description');
    var noDataContent = bizagi.localization.getResource('workportal-widget-admin-errorlogs-grid-nodatacontent');
    var dateTimeFormat = self.dateFormat + ' ' + self.timeFormat;

    $("#admin-errorlogs-grid", self.getContent()).jsGrid({
      autoload: true,
      controller: {
        loadData: function loadData(filter) {
          return self.dataService.getErrorLogs(filter);
        } },

      editing: false,
      fields: [
      {
        deleteButton: false,
        editButton: false,
        headerTemplate: function headerTemplate() {
          var checkboxH = $('<input id="grid-control-header" type="checkbox"></input>').on(
          'click', function () {
            if (!$(this).prop('checked')) {
              self.clearSelection();
            } else {
              $(this).prop('checked', true);
              $("#btn-admin-errorlogs-export").prop('disabled', false);
              $(".grid-control-item", self.getContent()).each(function () {
                $(this).prop('checked', true);
                self.CurrentClientData.selectedGuids.push($(this).prop('value'));
              });
            }
          });


          return checkboxH;
        },
        itemTemplate: function itemTemplate(value, item) {
          var checkboxI = $('<input class="grid-control-item" type="checkbox" value="' +
          item["Id"] + '" />').on('click', function () {
            if ($(this).prop('checked')) {
              self.CurrentClientData.selectedGuids.push($(this).prop('value'));
              $("#grid-control-header", self.getContent()).prop('checked', true);
              $("#btn-admin-errorlogs-export").prop('disabled', false);
            } else {
              var index = self.CurrentClientData.selectedGuids.indexOf(
              $(this).prop('value'));
              self.CurrentClientData.selectedGuids.splice(index, 1);
              if (self.CurrentClientData.selectedGuids.length == 0) {
                $("#btn-admin-errorlogs-export").prop('disabled', true);
                $("#grid-control-header", self.getContent()).prop('checked', false);
              } else {
                $("#btn-admin-errorlogs-export").prop('disabled', false);
                $("#grid-control-header", self.getContent()).prop('checked', true);
              }
            }
          });

          return checkboxI;
        },
        type: 'control',
        width: 50 },

      {
        align: 'center',
        css: "admin-errorlogs-grid-cell-truncate",
        name: 'Id',
        title: titleId,
        type: 'text' },

      {
        align: 'center',
        css: "admin-errorlogs-grid-cell-truncate",
        itemTemplate: function itemTemplate(value, item) {
          return bizagi.util.dateFormatter.formatDate(new Date(value), dateTimeFormat);
        },
        name: 'DateTime',
        title: titleDateTime,
        width: 100,
        type: 'text' },

      {
        align: 'center',
        css: "admin-errorlogs-grid-cell-truncate",
        name: 'UserName',
        title: titleUser,
        width: 120,
        type: 'text' },

      {
        align: 'center',
        css: "admin-errorlogs-grid-cell-truncate",
        name: 'CaseNumber',
        title: titleCase,
        width: 80,
        type: 'text' },

      {
        align: 'center',
        css: "admin-errorlogs-grid-cell-truncate",
        name: 'Node',
        title: titleNode,
        width: 120,
        type: 'text' },

      {
        align: 'left',
        css: "admin-errorlogs-grid-cell-truncate",
        itemTemplate: function itemTemplate(value, item) {
          var link = $('<span class="admin-errorlogs-grid-cell-detail-link">' + value +
          '</span>').on('click', function () {
            self.dataService.getErrorLogsContents([item["Id"]]).done(
            function (response) {
              if (response) {
                self.showDetailDialog(response);
              }
            });

          });


          return link;
        },
        name: 'Description',
        title: titleDescription,
        type: 'text' }],


      paging: true,
      pageButtonCount: 5,
      pageSize: 20,
      pageNextText: $('<svg class="bz-icon bz-icon-16 bz-icon-svg"><use xlink:href="#icon-arrow-next" /></svg>'),
      pagePrevText: $('<svg class="bz-icon bz-icon-16 bz-icon-svg"><use xlink:href="#icon-arrow-prev" /></svg>'),
      pageFirstText: $('<svg class="bz-icon bz-icon-16 bz-icon-svg""><use xlink:href="#icon-arrow-first" /></svg>'),
      pageLastText: $('<svg class="bz-icon bz-icon-16 bz-icon-svg"><use xlink:href="#icon-arrow-last" /></svg>'),
      pagerFormat: '{first} {prev} {pages} {next} {last} &nbsp;&nbsp; {pageIndex} / {pageCount}',
      noDataContent: noDataContent,
      width: '100%',

      onPageChanged: function onPageChanged(args) {
        self.clearSelection();
      },

      onRefreshed: function onRefreshed(args) {
        self.clearSelection();
      } });


    var lang = bizagi.util.languages[BIZAGI_LANGUAGE];
    if (lang && !lang.startsWith('en')) {
      var lang = lang.substring(0, 2);
      if (jsGrid.locales[lang])
      jsGrid.locale(lang);
    }
  },

  clearSelection: function clearSelection() {
    var self = this;

    $("#grid-control-header").prop('checked', false);
    $("#btn-admin-errorlogs-export").prop('disabled', true);
    $(".grid-control-item", self.getContent()).prop('checked', false);
    self.CurrentClientData.selectedGuids = [];
  },

  showDetailDialog: function showDetailDialog(data) {
    var self = this;

    var dateTimeFormat = self.dateFormat + ' ' + self.timeFormat;
    data[0].DateTime = bizagi.util.dateFormatter.formatDate(new Date(data[0].DateTime),
    dateTimeFormat);
    data[0].Content = atob(data[0].Content);

    $.tmpl(self.getTemplate("admin.errorlogs.detail"), { errorlog: data[0] }).
    appendTo("#admin-errorlogs-detail-placeholder");

    $("#admin-errorlogs-table").hide();
    $("#admin-errorlogs-detail-placeholder").show();

    self.setupBackButton();
    self.setupExportDetailButton();
  },

  setupBackButton: function setupBackButton() {
    $("#btn-admin-errorlogs-detail-back").click(function () {
      $("#admin-errorlogs-detail").remove();
      $("#admin-errorlogs-detail-placeholder").hide();
      $("#admin-errorlogs-table").show();
    });
  },

  setupExportDetailButton: function setupExportDetailButton() {
    var self = this;
    $("#btn-admin-errorlogs-detail-export", self.getContent()).click(function () {
      var currentErrorLogId = $("#admin-errorlogs-detail-info-current").text();
      self.dataService.getErrorLogsArchive([currentErrorLogId]).done(
      function (response) {
        if (response.ZipContent) {
          self.showExportDialog($("#admin-errorlogs-detail"), response);
        }
      });

    });
  },

  showExportDialog: function showExportDialog(parent, data) {
    var self = this;

    $.tmpl(self.getTemplate("admin.errorlogs.export"), { password: data.Password }).
    appendTo("#admin-errorlogs-export-placeholder");

    parent.hide();
    $("#admin-errorlogs-export-placeholder").show();

    self.setupCopypassButton();
    self.setupDownloadButton(data.ZipContent);
    self.setupCancelButton(parent);
  },

  setupCopypassButton: function setupCopypassButton() {
    var self = this;
    $("#admin-errorlogs-export-password-field", self.getContent()).tooltip({
      content: bizagi.localization.getResource('workportal-widget-admin-errorlogs-detail-tooltip'),
      items: "input[type=text]",
      position: { my: "left+4 center", at: "right+4 center" },
      show: false }).
    off("mouseover").off("focusin");
    $("#admin-errorlogs-export-password-field", self.getContent()).click(function () {
      $(this).focus();
      $(this).select();
      document.execCommand("copy");
      $(this).tooltip("open");
    });
  },

  setupDownloadButton: function setupDownloadButton(file) {
    var self = this;
    $("#btn-admin-errorlogs-export-download", self.getContent()).click(function () {
      var fileName = "BizagiErrorLogs.zip";
      var bytes = atob(file);
      var numbers = new Array(bytes.length);
      for (var i = 0; i < bytes.length; i++) {
        numbers[i] = bytes.charCodeAt(i);
      }
      var array = new Uint8Array(numbers);
      var blob = new Blob([array], { type: 'application/zip' });

      if (window.navigator && window.navigator.msSaveOrOpenBlob) {
        window.navigator.msSaveOrOpenBlob(blob, fileName);
      } else {
        var url = window.URL.createObjectURL(blob);
        var link = document.createElement('a');
        link.href = url;
        link.download = fileName;
        link.click();
        window.URL.revokeObjectURL(url);
      }
    });
  },

  setupCancelButton: function setupCancelButton(parent) {
    var self = this;
    $("#btn-admin-errorlogs-export-cancel", self.getContent()).click(function () {
      $("#admin-errorlogs-export-placeholder").hide();
      $("#admin-errorlogs-export").remove();
      parent.show();
    });
  } });